import server from '../../server.js';
export default {
	data() {
		return {
			shadow:true,
			isShowAD:true,
			modalName:'',
			showSC:false
		}
	},
	onLoad:function(){
		this.getShowSc()
		uni.getStorage({
		    key: 'showAD',
		    success: (res) => {
				console.log(res.data)
		        if(res.data=='true'){
					this.isShowAD=true 
					this.shadow = true
				}else{
					this.isShowAD=false
					this.shadow = false
				}
		    }
		});
		if(server.userinfo.VIPEndTime>0){
			this.isShowAD=false;
		}else{
			this.isShowAD=true;
			this.shadow = false
		}	
	},
	methods: {
		SETSC:function(){
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/user.php?type=setShowSC', //仅为示例，并非真实接口地址。
				data: {
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				success: (res) => {
					if(res.data.data=='off'){
						this.showSC=true
					}else{
						this.showSC=false
					}
					uni.showToast({
						title: res.data.msg,
						position:'bottom',
						icon:'none',
						position:'center'
					});
				}
			});
		},
		getShowSc(){
			uni.request({
				method:'GET',
				url: 'https://api.angeli.top/user.php?type=getShowSC', 
				data: {
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
				},
				success: (res) => {
					if(res.data.data=='off'){
						this.showSC=true
					}else{
						this.showSC=false
					}
					
				}
			});
		},
		kefu:function(){
			uni.navigateTo({
				url: '../menu/help'
			})
		},
		getyinsi:function(){
			uni.navigateTo({
				url: '../edit/edit?type=5'
			})
		},
		kaitong:function(){
			this.modalName=''
			uni.navigateTo({
				url: '../vip/vip'
			})
		},
		userAnquan:function(){
			uni.navigateTo({
				url: '../anquan/anquan'
			});
		},
		logout:function(){
			uni.clearStorage();
			uni.showToast({
				title: "即将退出登录",
				position:'bottom',
				icon:'none',
				position:'center'
			})
			server.userinfo=[];
			server.token=null;
			setTimeout(function () {
				uni.redirectTo({
					url:'../reg/reg'
				})
			}, 1500);
		},
		SetShadow(e) {
			this.shadow = e.detail.value
			if(this.shadow==false){
				console.log("先冲会员")
				if(server.userinfo.VIPEndTime>0){
					uni.setStorage({
					    key: 'showAD',
					    data: 'false',
					    success: function () {
					        console.log('success');
					    }
					});
				}else{
					this.modalName = 'Image'
					uni.setStorage({
					    key: 'showAD',
					    data: 'true',
					    success: function () {
					        console.log('success');
					    }
					});
				}
			}else{
				uni.setStorage({
				    key: 'showAD',
				    data: 'true',
				    success: function () {
				        console.log('success');
				    }
				});
			}
			
			console.log(this.shadow)
		},
		guanbi:function(){
			if(server.userinfo.VIPEndTime<=0){
				this.modalName = 'Image'	
			}
		},
		getInfo:function(){
			uni.navigateTo({
				url: '../editinfo/editinfo'
			});
		},
		getAbout:function(){
			uni.navigateTo({
				url:'../about/about'
			})
		}
	}
}
